.root {
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.appbar {
    background-color: #2d3748 !important;
}

.grid-container {
    -webkit-flex-grow: 1;
            flex-grow: 1;
}

.content-container {
    padding: 1rem;
    height: 100%;
    background-color: #edf2f7;
}

.content-container .title {
    font-size: 1.25rem;
    font-weight: 600;
}

.content-container .color-block {
    border-radius: 100%;
    display: inline-block;
    min-height: 30px;
    min-width: 30px;
}

.content-container .color-block-container {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    margin-bottom: 10px;
}

.content-container .color-block {
    margin-right: 10px;
}

.content-container .color-block.source {
    border: 2px solid black;
}

.content-container .color-block.destination {
    border: 2px dashed black;
}

.content-container .color-block.blue {
    background-color: #b2ebf2;
}

.content-container .color-block.green {
    background-color: #a5d6a7;
}

.content-container .color-block.grey {
    background-color: #b1b1b7;
}

.content-container .color-block.yellow {
    background-color: #ffd54f;
}

.graph-container {
    position: relative;
    width: 100%;
    min-height: 100%;
}

.graph-container .message {
    position: absolute;
    font-size: 20px;
    line-height: 25px;
    top: 0;
    width: 100%;
}

.graph-container .message .MuiSvgIcon-root {
    width: 25px;
    height: 25px;
}

#notistack-snackbar {
    padding: 12px 14px;
    line-height: 25px;
    vertical-align: middle;
    font-size: 18px;
}

#notistack-snackbar .MuiSvgIcon-root {
    width: 25px;
    height: 25px;
    padding-right: 5px;
}

/* Arbitary */

.color-bubble {
    height: 30px;
    width: 30px;
    border-radius: 50%;
    border: solid black;
    background-color: blue;
    margin-bottom: auto;
    margin-top: auto;
    margin-right: 10px;
}
.color-code {
    display: -webkit-flex;
    display: flex;
}

.instruction {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: rgb(202, 144, 123);
    color: white;
}

.instruction p {
    font-size: 25px;
    font-weight: 400;
}

.footer {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}
.footer p,
.footer input,
.footer button {
    margin: 10px;
}

.footer > button {
    background-color: rgb(32, 41, 53);
    font-size: medium;
    color: white;
    border-radius: 15%;
    cursor: pointer;
}
.content {
    width: 100%;
    display: grid;
    grid-template-columns: 35% 65%;
    height: 100%;
    box-sizing: border-box;
}
.left-div {
    background-color: rgb(237, 242, 247);
    padding: 20px;
    /* height: 93vh; */
    overflow: scroll;
}
.right-div {
    /* height: 100%; */
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.graph {
    width: 100%;
    height: 80%;
}

.copyright {
    --bg-opacity: 1;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: rgba(160, 174, 192, var(--bg-opacity));
    height: 20px;
}

.copyright a {
    --text-opacity: 1;
    color: rgba(44, 82, 130, var(--text-opacity));
}

.copyright h3,
.copyright a {
    margin: 0;
    padding: 0;
    font-size: 10px;
}
